import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import About from "../views/About.vue";
import Profile from "../components/user/Profile.vue";
import Signup from "../components/user/Signup.vue";
import Board from "../components/board/Board.vue";
import BoardDetail from "../components/board/BoardDetail";
import BoardCreate from "../components/board/BoardCreate";
import Book from "../components/book/Book.vue";
import Conference from "../components/conference/Conference.vue";
import Conference_detail from "../components/conference/Conference_detail.vue";
import store from "../store/index";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/about",
    name: "About",
    component: About,
  },
  {
    path: "/profile",
    name: "Profile",
    component: Profile,
  },
  {
    path: "/signup",
    name: "Signup",
    component: Signup,
  },
  {
    path: "/board",
    name: "Board",
    component: Board,
  },
  {
    path: "/board/detail/:contentId",
    name: "BoardDetail",
    component: BoardDetail,
  },
  {
    path: "/board/create/:contentId?",
    name: "BoardCreate",
    component: BoardCreate,
  },
  {
    path: "/book",
    name: "Book",
    component: Book,
  },
  {
    path: "/conference",
    name: "Conference",
    component: Conference,
  },
  {
    path: "/conference/view",
    name: "Conference_detail",
    component: Conference_detail,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  store.commit("startSpinner");
  setTimeout(() => {
    next();
  }, 30);
});

// router.afterEach((to, from) => {
//   store.commit("endSpinner");
// });

export default router;
